import React, { Component } from 'react'
import * as Tangle from 'react-tangle-result'
import logo from './logo.svg'
import './App.css'

const TangleText = Tangle.TangleText
const TangleResult = Tangle.TangleResult.default

// const TangleText2 = require('../node_modules/react-tangle/index.js')

class App extends Component {
  constructor (props) {
    super(props)

    this.state = {
      value: 0,
      result: 2,
      reverseResult: 2
    }

    this.resultChange = this.resultChange.bind(this)
    this.valueChange = this.valueChange.bind(this)
  }

  resultChange (value) {
    this.setState({
      value: value,
      result: value + 2,
      reverseResult: 2 - value
    })
  }

  valueChange (value) {
    this.setState({
      value: value,
      result: value + 2,
      reverseResult: 2 - value
    })
  }

  render () {
    return (
      <div className='App'>
        <div className='App-header'>
          <img src={logo} className='App-logo' alt='logo' />
          <h2>Welcome to React</h2>
        </div>
        <h4>Without react-tangle-result</h4>
        <div className='row'>
          <span>2 -</span>
          <TangleText value={this.state.value} onChange={this.valueChange} onInput={this.resultChange} />
          <span>= {this.state.result}</span>
        </div>

        <h4>With react-tangle-result</h4>
        <div className='row'>
          <span>2 -</span>
          <TangleText value={this.state.value} onChange={this.valueChange} onInput={this.resultChange} percision={1} />
          <span> = </span>
          <TangleResult value={this.state.reverseResult} />
        </div>
      </div>
    )
  }
}

export default App



